import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';



/**
 * Services for managing club activities - for account administrator only
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class UsrActivitiesService {

  // URL to web api
  private baseUrl = '/user/activities';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Get single activity by id
   * @Return: EntityResponse<Activity>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Find activities by time range
   * @Return: QueryResponse<Activity>
   */
  find(from?: number, to?: number, year?: number, month?: number, day?: number, total?: boolean) {
    const params = new Array();		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (year != null) { params.push(`year=${year}`); }
    if (month != null) { params.push(`month=${month}`); }
    if (day != null) { params.push(`day=${day}`); }
    if (total != null) { params.push(`total=${total}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

}
