import { DayOfWeekCode } from '../enums/DayOfWeekCode';
import { ResourceTypeMask } from '../enums/ResourceTypeMask';
import { AccountRoleCode } from '../enums/AccountRoleCode';

/* 
 *  Recurrent activity
*/
export class RecurrentActivity {
 
    // Day of week - based on ISO8601 
    public dayOfWeek: DayOfWeekCode;
 
    // Start time [number of minutes start from 00:00 UTC] 
    public startTime: number;
 
    // End time [number of minutes start from 00:00 UTC] 
    public endTime: number;
 
    // Name of the activity 
    public name: string;
 
    // Activity is restricted to specific resource type 
    public resourceFilter: ResourceTypeMask[];
 
    // Activity is restricted to specific role (members, para, team) 
    public roleFilter: AccountRoleCode[];
 
    constructor(dayOfWeek?: DayOfWeekCode, startTime?: number, endTime?: number, name?: string, resourceFilter?: ResourceTypeMask[], roleFilter?: AccountRoleCode[]) { 
        this.dayOfWeek = dayOfWeek;
        this.startTime = startTime;
        this.endTime = endTime;
        this.name = name;
        this.resourceFilter = resourceFilter;
        this.roleFilter = roleFilter;
    }

}


/*
 *  Interface of RecurrentActivity
*/
export interface IRecurrentActivity {
 
    // Day of week - based on ISO8601 
    dayOfWeek?: DayOfWeekCode;
 
    // Start time [number of minutes start from 00:00 UTC] 
    startTime?: number;
 
    // End time [number of minutes start from 00:00 UTC] 
    endTime?: number;
 
    // Name of the activity 
    name?: string;
 
    // Activity is restricted to specific resource type 
    resourceFilter?: ResourceTypeMask[];
 
    // Activity is restricted to specific role (members, para, team) 
    roleFilter?: AccountRoleCode[];
 
}
